@import url(https://fonts.googleapis.com/css2?family=Andika+New+Basic&display=swap);
.movie {
  width: 45%;
  background-color: rgb(41, 40, 40);
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 10px 15px 15px #1d1c1c;
  color: #cecece;
}

.movie a {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  text-decoration: none;
  color: inherit;
}

.movie img {
  max-width: 180px;
  margin-right: 30px;
  position: relative;
  top: -50px;
  width: 100%;
  box-shadow: 5px 10px 10px #000000;
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #ffffff;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  margin: 5px 0px;
}

.movie .movie__genres li {
  margin-right: 10px;
}

@media screen and (max-width: 768px) {
  .movie {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
  }

  .movie a {
    display: flex;
    flex-direction: column;
    text-decoration: none;
    text-align: center;
    color: inherit;
  }

  .movie .movie__genres li {
    margin-right: 5px;
    margin: auto;
  }

  .movie img {
    max-width: 180px;
    position: relative;
    top: -50px;
    width: 100%;
    margin: 0 auto;
    box-shadow: 5px 10px 10px #000000;
  }
}

@media screen and (min-width: 769px) and (max-width: 1200px) {
  .movie {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
  }

  .movie a {
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: inherit;
  }

  .movie img {
    max-width: 180px;
    position: relative;
    top: -50px;
    width: 100%;
    box-shadow: 5px 10px 10px #000000;
  }
}

* {
    box-sizing: border-box;
    font-family: 'Andika New Basic', sans-serif;
}

body {
    margin: 0;
    padding: 0;
    background-color: #f0c6c6;
    height: 100%;
}

html,
body,
#root,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
    color: #292727;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}
.detail__background {
  background: center/center no-repeat;
  position: absolute;
  left: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

.detail__container {
  left: 0px;
  position: absolute;
  background-color: #1d1c1c;
  color: #cecece;
  width: 100%;
  height: 100%;
  opacity: 85%;
  text-align: center;
}

.detail__poster {
  opacity: 100%;
  margin-top: 10%;
  width: 250px;
}

.detail__title,
.detail__year {
  margin: 0;
  font-weight: 300;
}

.detail__title {
  font-size: 30px;
  color: #ffffff;
}

.detail__year {
  font-size: 16px;
}

.detail__genres {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 18px;
  padding: 0px;
}

.detail__genres li {
  margin-right: 10px;
}

.detail__summary {
  overflow-wrap: break-word;
  margin: 20px;
  font-size: 20px;
}

@media screen and (max-width: 768px) {
  .detail__background {
    display: none;
  }

  .detail__container {
    left: 0px;
    position: absolute;
    background-color: #1f1f1f;
    color: #cecece;
    width: 100%;
    height: 130%;
    opacity: 100%;
    text-align: center;
  }
}

.navibar {
  z-index: 1;
  position: fixed;
  left: 30px;
  top: 30px;
  color: #ffffff;
  font-size: 25px;
  width: 30px;
}

.navibar a {
  color: #ffffff;
}

